.not-boot-container {
  margin: 0;
}
.login-container {
    display: flex;
    flex-direction: column;
    /* margin: 20%; */
    margin: 20%;
}
.blog-container {
    margin-bottom: 4em;
    margin: 50px 20% 0 20%;
    padding: 1.2em 0px;
    height: 60vh;
}

.blog-container li {
    list-style-type: none;
}

.blog-container p {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.read-more {
    display:flex;
    justify-content: flex-end;
    font-weight: 600;
}
.blog-container ul {
    padding: 0;
}

.blog-container ul, .blog-container p {
    padding: 0;
    width: 80%;
}
/* .scew-container { */
    /* background: black;
    color: white;
} */
.blog-container li, .del, .read-more{
    
cursor: pointer;
}
.addtask-form{
    display: flex;
    width: 80%;
    margin: 50px 0px;
}
.addtask-form input{
    height: 40px;
    width: 80%;
    font-size: 1em;
}
.addtask-form button{
    width: 20%;
    font-size: 1.2em;
}
.task-container p{
    font-size: 1.2em;
}
.task-container {
    margin: 100px 20% 100px 20%;
    height: 20vh;
}
#task-list:hover {
    text-decoration: line-through;
}
.addtask-form button {
    background: black;
    color: white;
    font-size: 1em;
}
.script-echo h3:nth-child(3){
    text-transform: capitalize;
}
.of-old-hat h3:nth-child(3){
    text-transform: capitalize;
}
.dribb {
    margin: 170px 10% 150px 10%;
    display: flex;
    justify-content: space-between;
}

.dribb .flex-bs {
    flex-basis: 30em;
}
h3 {
    text-align: center;
}
.footer-container {
    clip: rect(10px,400px,100px,138px);
    position: absolute;
    width: 100%;
}
.spotifyPlaylist{
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 10em;
}
.spotifyPlaylist iframe {
    flex-basis: 25%;
}
.spotify h1 {
    margin: 2em 3em;
    font-size: 2.5em;
}
.youtube h1{
    margin: 2em 3em;
    font-size: 2.5em;
}
.youtube iframe{
    display: block;
	width: 75%;
	/* height: auto; */
	margin: 0 auto;
}
.youtube {
    margin-bottom: 10em;
}
.reddit {
    display: flex;
    justify-content: space-evenly;
    margin-left: 2em;
    margin-right: 2em;
    margin-bottom: 7em;
}
.reddit .sub-reddit {
    flex-basis: 25%;
}
.header{
    font-size: 2.5em;
    margin: 1.5em 3em;
}
.reddit p {
    font-weight: 700;
}




.title-right{
    position: absolute;
    -webkit-writing-mode: vertical-lr;
        -ms-writing-mode: tb-lr;
            writing-mode: vertical-lr;
    right: 20%;
    -webkit-text-orientation: upright;
            text-orientation: upright;
    -webkit-text-emphasis: triangle;
}
.blog-text {
    /* color: white; */
    padding: 20px 0px 0px 0px;
}

.task-text {
    padding-top: 30px;
}
.grey-container {
    display: flex;
    justify-content: space-evenly;
    /* padding: 32px; */
    margin:3.5em;
    margin-top: 10em;
    /* margin-bottom: 15em; */
   
}
.grey-container .script-echo {
    flex-basis: 25%;
    /* text-align: left; */
    text-align: center;
}
.grey-container .book-logs {
    flex-basis: 25%;
    text-align: center;
}
.grey-container .of-old-hat {
    flex-basis: 25%;
    text-align: center;
}
.grey-container h1 {
    font-size: 2.5em;
    margin-bottom: 1.5em;
}
